import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { Course } from '../course-catalog/course.model';

@Injectable({
  providedIn: 'root'
})
export class CartService {

  cartListChanged = new Subject<Course[]>();
  private cartList: Course[] = [];

  constructor() { }

  getCartList() {
    return this.cartList.slice();
  }

  addToCart(course: Course) {
    this.cartList.push(course);
    this.cartListChanged.next(this.cartList.slice());
  }

  removeFromCart(course: Course) {
    this.cartList = this.cartList.filter((c: Course) => {
      return c.id !== course.id
    });
    this.cartListChanged.next(this.getCartList());
  }

  isExistInCart(course: Course) {
    return this.cartList.find(c => c.id === course.id);
  }

  calculateTotalPrice() {
    if (this.cartList) {
      let totalPrice =0;
      this.cartList.forEach((course: Course) => {
        totalPrice += course.price;
      });
      return totalPrice;
    }
  }
}
