import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { CartService } from 'src/app/cart/cart.service';
import { Course } from 'src/app/course-catalog/course.model';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  countCartList: number = 0;
  showCart: boolean = false;

  constructor(private cartService: CartService) { }

  ngOnInit() {
    this.countCartList = this.cartService.getCartList().length;
    this.cartService.cartListChanged.subscribe(
      (courses: Course[]) => this.countCartList = courses.length
    );
  }

  showCartToggle() {
    this.showCart = !this.showCart;
    console.log(this.showCart);
  }

}
