import { Component, OnInit } from '@angular/core';
import { CartService } from './cart.service';
import { Course } from '../course-catalog/course.model';
import { Subscription } from 'rxjs';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit {

  cartList: Course[] = [];
  subscription: Subscription;
  totalPrice: number = 0;


  constructor(
    private cartService: CartService,
    private toastrService: ToastrService
  ) { }

  ngOnInit() {
    this.cartList = this.cartService.getCartList();
    this.totalPrice = this.cartService.calculateTotalPrice();
    this.subscription = this.cartService.cartListChanged.subscribe(
      (cartCourses: Course[]) => {
        this.cartList = cartCourses;
        this.totalPrice = this.cartService.calculateTotalPrice();
      }
    );
  }

  removeFromCart(course: Course) {
    this.cartService.removeFromCart(course);
    this.showToastr(course.name);
  }

  showToastr(itemName) {
    this.toastrService.error(itemName + ' course removed from your cart');
  }
}


